
// Variablen
var sidebarbox = document.getElementById("sidebar");
var sidebar = document.getElementById("sidebarinnline");
var height = window.innerHeight;
var menustatus = false;
var x, y;
var path =
	"M 0 0 L 100 0 Q 100 " +
	height / 100 * 25 +
	" 100 " +
	height / 2 +
	" Q 100 " +
	height / 100 * 75 +
	" 100 " +
	height +
	" L 0 " +
	height +
	" z";

// Übergabe unseres Path an die Grafik
sidebar.setAttribute("d", path);

// Mouse Move
document.onmousemove = function() {
	x = event.clientX;
	y = event.clientY;
	if (x <= 300) {
		path =
			"M 0 0 L 100 0 Q 130 " +
			(y - 150) +
			" 130 " +
			y +
			" Q 130 " +
			(y + 150) +
			" 100 " +
			height +
			" L 0 " +
			height +
			" z";
	} else {
		path =
			"M 0 0 L 100 0 Q 100 " +
			(y - 150) +
			" 100 " +
			y +
			" Q 100 " +
			(y + 150) +
			" 100 " +
			height +
			" L 0 " +
			height +
			" z";
	}
	if (menustatus == false) {
		sidebar.setAttribute("d", path);
	}
};

sidebarbox.addEventListener("click", function() {
	if (menustatus == false) {
		open();
	} else {
		close();
	}
});

function open() {
	path =
		"M 0 0 L 450 0 Q 450 " +
		height / 100 * 25 +
		" 450 " +
		height / 2 +
		" Q 450 " +
		height / 100 * 75 +
		" 450 " +
		height +
		" L 0 " +
		height +
		" z";
	sidebar.setAttribute("d", path);
	sidebarbox.classList.add("open-sidebar");
	menustatus = true;
}

function close() {
	path =
		"M 0 0 L 100 0 Q 100 " +
		height / 100 * 25 +
		" 100 " +
		height / 2 +
		" Q 100 " +
		height / 100 * 75 +
		" 100 " +
		height +
		" L 0 " +
		height +
		" z";
	sidebar.setAttribute("d", path);
	sidebarbox.classList.remove("open-sidebar");
	menustatus = false;
}




class MatrixAnimation {
    /**
     * 
     * @param {object} element HTML Object in dem der Matrix Effekt gerendert werden soll
     * @param {string} lettersColor Farbe der Schrift
     * @param {number} letterSize Größe der Schrift
     * @param {*} font Schriftfamilie
     * @param {*} speed Geschwindigkeit
     */
    constructor(element, lettersColor, letterSize, font, speed) {
        this.element = element;
        this.width = element.offsetWidth;
        this.height = element.offsetHeight;
        this.lettersColor = lettersColor;
        this.letterSize = letterSize;
        this.font = font;
        this.speed = speed < 24 ? 24 : speed;
        this.status = true;
        this.ctx = undefined;
        this.letters = undefined;
    }

    drawAnimation() {

        if (this.status) {
            this.ctx.fillStyle = this.lettersColor;
            this.ctx.font = `${this.letterSize}pt ${this.font}`;
    
            this.letters.forEach((y, index) => {
                const randomSymbol = String.fromCharCode(Math.random() * 128);
                const x = index * this.letterSize;
    
                this.ctx.fillText(randomSymbol, x, y);
    
                if (y > 250 + Math.random() * 15000) this.letters[index] = 0;
                else this.letters[index] = y + this.letterSize;
            });
    
            this.ctx.fillStyle = `rgba(0, 0, 0, 0.1)`;
            this.ctx.fillRect(0, 0, this.width, this.height);
        }

    }

    createCanvas(element) {
        this.element.innerHTML = '';

        this.width = element ? element.offsetWidth : this.width;
        this.height = element ? element.offsetHeight : this.height;

        const canvas = document.createElement('canvas');
        canvas.setAttribute('id', 'canvas');
        canvas.setAttribute('width', this.width);
        canvas.setAttribute('height', this.height);

        this.ctx = canvas.getContext('2d');

        const col = Math.floor(this.width/this.letterSize);
        this.letters = Array(col).fill(0);

        this.element.append(canvas);

    }

    init() {
        this.createCanvas();

        setInterval(() => {
            this.drawAnimation();
        }, this.speed);

    }

    pausePlay() {
        this.status = this.status ? false : true;
    }

}

const matrix = new MatrixAnimation(document.querySelector('#matrix'), '#03fc35', 10, 'Arial', 80);

matrix.init();

window.addEventListener('resize', () => {
    matrix.createCanvas(document.querySelector('#matrix'));
});


